body {
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.5;
  margin: 0; }

a {
  color: black;
  text-decoration: none; }

a:hover,
a:focus {
  outline: none; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }
